function FAQ() {
  const [activeAccordion, setActiveAccordion] = useState(null);

  const handleAccordionClick = (index) => {
    if (activeAccordion === index) {
      setActiveAccordion(null);
    } else {
      setActiveAccordion(index);
    }
  };

  const isAccordionOpen = (index) => {
    return activeAccordion === index;
  };

  return (
    <section id="FAQ" className="get-started">
    <div className="container ">
    <div className="row text-center ">
      <h1 className="display-3 fw-bold text-capitalize">FAQ</h1>
   
    </div>
          <div className="heading-line"></div>
          <p className="lead  text-center">frequently asked questions, get knowledge beforehand</p>
        
   
        <div className="row mt-5">
          <div className="col-md-12">
            <div className="accordion" id="accordionExample">
              {faqData.map((item, index) => (
                <div className="accordion-item shadow mb-3" key={index}>
                  <h2 className="accordion-header" id={`heading${index}`}>
                    <button
                      className={`accordion-button ${isAccordionOpen(index) ? '' : 'collapsed'}`}
                      type="button"
                      onClick={() => handleAccordionClick(index)}
                    >
                      {item.question}
                    </button>
                  </h2>
                  <div
                    id={`collapse${index}`}
                    className={`accordion-collapse collapse ${isAccordionOpen(index) ? 'show' : ''}`}
                    aria-labelledby={`heading${index}`}
                    data-bs-parent="#accordionExample"
                  >
                    <div className="accordion-body">
                      {item.answer}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
   </section>
 
  );
}

export default FAQ;
